import React from 'react'
import Togglable from './Togglable'
import { useSelector } from 'react-redux'
// eslint-disable-next-line react/prop-types
const ImageDesc = ({ imageDesc }) => {

  return (
    <div>
      {imageDesc}
    </div>
  )
}


const Apod = () => {
  console.log('we\'re in apod')
  const apod = useSelector(state => state.apod)


  console.log(apod)

  if (apod === undefined) {
    console.log(apod)

    return null
  }

  if (apod !== undefined) {
    return (
      <div className='content'>
        <h2>Astronomy Picture of the Day</h2>
        <div key={apod.url}>
          <div className='apodImg'>

            <img src={apod.url} alt={apod.title} />
            <br />
            <b>(c) {apod.copyright} </b>
            <br />
            {apod.date}
            <br />
            <br />
          </div>
          <Togglable toggLabel='See description'>
            <b>Description:</b>
            <ImageDesc imageDesc={apod.explanation} />
          </Togglable> </div>
        <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://apod.nasa.gov/apod/' target="_blank" rel="noopener noreferrer">APOD on Nasa homepage</a></div>
        <div>
        </div>
      </div>
    )
  }
}


export default Apod